<template lang="pug">
    header.header
        .container
            .header__content
                MenuBtn
                a.header__brand
                    svg.icon-logo.logo
                      use(xlink:href='#icon-logo')
                .header__search.search
                    input.search__input(type="search"
                        placeholder="Search by word, listing or store name")
                    a(href="#").search__btn.header__link.detail-search Detail Search
                nav.nav.header__nav
                    router-link.header__link(to="/") Login / Sign Up
                    Button(color="green") VIP Membership
</template>

<style lang="scss">
.search {
  width: 300px;
  max-width: 100%;
}

.logo {
  max-width: 100%;
  height: 50px;
  width: 50px;
}

.header {
  background-color: $headerBg;
  box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.06);

  &__link {
    color: #111;
    text-decoration: none;
    padding: 15px;

    &:hover {
      text-decoration: underline;
    }
  }

  &__brand {
    display: block;
    text-align: center;

    @include respond-to("tablet-l") {
      text-align: left;
    }
  }

  &__search {
    @include respond-to("tablet-l") {
      padding-left: 80px;
      display: table-cell;
    }
  }

  &__content {
    width: 100%;
    text-align: left;
    position: relative;
    padding-top: 10px;

    @include respond-to("tablet-l") {
      padding-top: 0;
      display: table;
      height: $headerHeight;
    }

    > * {
      @include respond-to("tablet-l") {
        display: table-cell;
        vertical-align: middle;
      }

      &:last-child {
        text-align: right;
      }
    }
  }

  &__nav {
    display: none;

    @include respond-to("screen-s") {
      display: table-cell;
    }
  }
}

.search {
  display: table;
  width: 100%;
  border-spacing: 10px;

  @include respond-to("tablet-l") {
    display: table-cell;
    width: auto;
  }

  &__input {
    display: table-cell;
    vertical-align: middle;
    width: 100%;

    @include respond-to("tablet-l") {
      width: 350px;
      max-width: 100%;
    }
  }

  &__btn {
    display: table-cell;
    vertical-align: middle;
    padding: 0;

    @include respond-to("tablet-l") {
      display: inline-block;
      padding: 15px;
    }
  }
}
</style>

<script>
import Button from "@/components/Button.vue";
import MenuBtn from "@/components/MenuBtn.vue";

export default {
  components: {
    Button,
    MenuBtn,
  },
};
</script>
