<template lang="pug">
    button.btn(:class="btnColor" type='button')
      slot
</template>

<script>
export default {
  props: {
    color: {
      type: String,
    },
  },
  computed: {
    btnColor: function btnclass() {
      return `btn--${this.color}`;
    },
  },
};
</script>

<style lang="scss">
.btn {
  padding: 0 20px;
  border-radius: 20px;
  border: none;
  line-height: 43px;
  display: inline-block;
  font-size: 1.1rem;

  &:hover {
      box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.06);
  }

  &--primary {
    background: $primaryColor;
    color: #fff;
  }

  &--green {
    background: $green;
    color: #fff;
  }
}
</style>

