<template lang="pug">
  div.property
    .property__img
      img(:src="require('@/assets/house.jpg')")
      .property__links
        a(href="#").property__link
          span.property__icon
            svg.icon-like
              use(xlink:href='#icon-like')
          span Add to favorites
        a(href="#").property__link
          span.property__icon
            svg.icon-visibility
              use(xlink:href='#icon-visibility')
          span View detail
        a(href="#").property__link
          span.property__icon
            svg.icon-list
              use(xlink:href='#icon-list')
          span Compare property
    .property__header
      Countdown(:deadline="deadline" v-if="deadline")
      div(v-if="!deadline")
        h3.property__title Bright Island Route
        span.property__price $ 40.000
    .property__info
      dl
        dt Type
        dd House
        dt Location
        dd New York
        dt Area
        dd 180 sqft

</template>

<style lang="scss">
.property {
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.08);
  transition: box-shadow 0.15s linear;

  &:hover {
    box-shadow: 0 2px 3px rgba(0, 0, 0, 0.12);

    .property__links {
      opacity: 1;
    }

    .property__header {
      background-color: #0bb7a5;
    }
  }

  &__img {
    position: relative;
    z-index: 1;

    img {
      width: 100%;
    }
  }

  &__icon {
    width: 1.5rem;
    height: 32px;
    line-height: 0;
    fill: #ffffff;
    padding-right: 10px;

    svg {
      width: 100%;
      height: 100%;
    }
  }

  &__info {
    font-size: 0.95em;
    padding: 20px;

    dl {
      @include clearfix;
    }

    dt {
      clear: left;
      float: left;
      line-height: 30px;
    }

    dd {
      clear: right;
      float: right;
      line-height: 30px;
    }
  }

  &__header {
    background-color: #4e4a4a;
    color: #fff;
    padding: 14px 20px;
    transition: background-color 0.15s linear;
  }

  &__links {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 100%;
    z-index: 9;
    opacity: 0;
    transition: opacity 0.15s linear;
    overflow: hidden;
  }

  &__link {
    color: #fff;
    height: 34%;
    display: table;
    padding-left: 20px;
    box-sizing: border-box;
    width: 100%;
    text-decoration: none;
    transition: background-color 0.15s linear;

    > * {
      display: table-cell;
      vertical-align: middle;
    }

    &:nth-child(1) {
      background-color: rgba(7, 111, 100, 0.9);

      &:hover {
        background-color: rgba(7, 111, 100, 1);
      }
    }

    &:nth-child(2) {
      background-color: rgba(8, 135, 122, 0.9);

      &:hover {
        background-color: rgba(8, 135, 122, 1);
      }
    }

    &:nth-child(3) {
      background-color: rgba(10, 159, 143, 0.9);

      &:hover {
        background-color: rgba(10, 159, 143, 1);
      }
    }
  }
}
</style>

<script>
import Countdown from "@/components/Countdown.vue";

export default {
  props: {
    deadline: {
      type: String,
    },
    price: {
      type: String,
      required: true,
    },
    address: {
      type: String,
      required: true,
    },
  },
  components: {
    Countdown,
  },
};
</script>
