<template lang="pug">
  .banner
    .banner__info
      h2.banner__title Newyork 3 + 1
      span.banner__sub-title 120#[sup m2] / Pool / Sauna / #[span.text-secondary Newyork]
      .banner__price
        span 80.000,000
        span $ / Starting Price
      Button.banner__btn(color="primary") GIVE OFFER
    .banner__img
      img(:src="require('@/assets/house.jpg')", alt="alt")
      .banner__current
        Countdown(deadline="December 22, 2018")
</template>

<script>
import Countdown from "@/components/Countdown.vue";
import Button from "@/components/Button.vue";

export default {
  props: {
    title: {
      type: String,
      required: true,
    },
  },
  components: {
    Countdown,
    Button,
  },
};
</script>

<style lang="scss">
.banner {
  @include wrap;
  @include wrap("middle");
  @include wrap(1);

  @include respond-to("tablet-l") {
    @include wrap(2);
  }

  background-color: $bannerBg;
  box-sizing: border-box;
  margin-bottom: 15px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.08);

  &__title {
    font-size: 3rem;
  }

  &__btn {
    @include respond-to("tablet-l") {
      position: absolute;
      top: 100%;
      margin-top: 20px;
      left: 50%;
      transform: translateX(-50%);
    }

    margin-top: 20px;
  }

  &__price {
    margin-top: 15px;

    > span {
      font-size: 1.1rem;
      display: block;
    }
  }

  &__info {
    @include col;
    text-align: center;
    position: relative;
    padding: 40px;

    @include respond-to("tablet-l") {
      padding: 0;
    }
  }

  &__current {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;

    .countdown {
      background-color: $bannerBg;
      padding: 10px 20px;
      box-shadow: 0px 0px 12px 0px rgba(0, 0, 0, 0.3);
    }

    .banner__price {
      background-color: $bannerBg;
      padding: 3px;
      display: inline-block;
    }
  }

  &__img {
    @include col;
    position: relative;

    img {
      max-width: 100%;
    }
  }
}
</style>
