<template lang="pug">
    a(href="javascript:void(0)" @click="menuOpen")#toggle
     span
</template>

<script>
export default {
  methods: {
    menuOpen() {
      document.getElementById("toggle").classList.toggle("on");
      document.getElementById("aside").classList.toggle("on");
      document.getElementById("app").classList.toggle("menu-open");
    },
  },
};
</script>

<style lang="scss" scoped>
#toggle {
  display: block;
  width: 28px;
  height: 30px;
  position: absolute;
  top: 40px;
  right: 10px;
  z-index: 999;

  @include respond-to("screen-s") {
    display: none;
  }
}

#toggle span:after,
#toggle span:before {
  content: "";
  position: absolute;
  left: 0;
  top: -9px;
}
#toggle span:after {
  top: 9px;
}
#toggle span {
  position: relative;
  display: block;
}

#toggle span,
#toggle span:after,
#toggle span:before {
  width: 100%;
  height: 3px;
  background-color: #888;
  transition: all 0.3s;
  backface-visibility: hidden;
  border-radius: 2px;
}

/* on activation */
#toggle.on span {
  background-color: transparent;
}
#toggle.on span:before {
  transform: rotate(45deg) translate(5px, 5px);
}
#toggle.on span:after {
  transform: rotate(-45deg) translate(7px, -8px);
}
#toggle.on + #menu {
  opacity: 1;
  visibility: visible;
}
</style>
